import request from '../utils/request';

/**
 * 客户基础资料
 * @params unionid 客户unionid externaluserid 客户userid userId 理财师userId
 */
export async function fetchCustomerInfo(params) {
  console.log(params)
  return request(`/v1/customerInfo/detail`, {
    method: 'POST',
    data: params
  })
}

