import { Reducer, Effect } from '../../models/connect';
import { fetchCustomerInfo, fetchDataOverview, fetchUserDeparment } from '../../services/detail';
import { SetCookie, EncryptDes } from '../../utils/func';

export interface DepartmentState {
  department: string;
  name: string;
  role: string;
  userid: string;
}

export interface UserState {
  userData: any;
  dataOverview: any;
  department: DepartmentState;
}

export interface UserModelType {
  namespace: string;
  state: UserState,
  effects: {
    fetchExternalData: Effect;
    fetchDataOverview: Effect;
    fetchUserDeparment: Effect;
  },
  reducers: {
    saveExternalData: Reducer;
    saveDataOverview: Reducer;
    saveUserDeparment: Reducer;
  },
}

const UserModel: UserModelType = {
  namespace: 'user',
  state: {
    userData: undefined,
    dataOverview: undefined,
    department: {
      department: '',
      name: '',
      role: '',
      userid: '',
    }
  },
  effects: {
    *fetchExternalData({ payload }, { call, put }) {
      const resp: any = yield call(fetchCustomerInfo, payload);

      if (resp.code === 200 && resp.data) {
        SetCookie('tid', EncryptDes(resp.data.train.id))

        yield put({
          type: 'user/saveExternalData',
          payload: {
            unionid: payload.unionid,
            externaluserid: payload.externaluserid,
            userData: resp.data
          }
        })
      } else {
        yield put({
          type: 'user/saveExternalData',
          payload: {
            unionid: payload.unionid,
            externaluserid: payload.externaluserid,
            userData: []
          }
        })
      }
    },
    *fetchDataOverview({ payload }, { call, put }) {
      const resp: any = yield call(fetchDataOverview, payload);

      if (resp.code === 200 && resp.data) {
        yield put({
          type: 'user/saveDataOverview',
          payload: {
            dataOverview: resp.data
          }
        })
      }
    },
    *fetchUserDeparment({ payload }, { call, put }) {
      const resp: any = yield call(fetchUserDeparment, payload);

      if (resp.code === 200 && resp.data) {
        yield put({
          type: 'user/saveUserDeparment',
          payload: resp.data
        })
      }
      console.log(resp)
    }
  },
  reducers: {
    saveExternalData(state, action) {
      const { payload } = action;

      return {
        ...state,
        unionid: payload.unionid,
        externaluserid: payload.externaluserid,
        userData: payload.userData
      }
    },
    saveDataOverview(state, action) {
      const { payload } = action;
      
      return {
        ...state,
        dataOverview: payload.dataOverview,
      }
    },
    saveUserDeparment(state, action) {
      const { payload } = action;

      return {
        ...state,
        department: payload
      }
    }
  }
}

export default UserModel;